
body, html {
  box-sizing: border-box;
  height: 100%;
  width: 100%;
  margin: 0;
  background-color: #15202B;
}

#root{  
  height: 100%;
  width: 100%;
}

.App{  
  height: 100%;
  max-width: 100%;
  font-family: sans-serif;
  display:flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6{
  color: white;
  padding: 0;
  margin:0;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-bar{
  position: absolute; 
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  width: 80%;
  height: 40px;
  background-color: #343A40;
  border-radius:5px;
}


.menu-item{
  text-decoration: none;
  padding: 10px;
  color: white;
  cursor: pointer;
  font-size: 12px;
}

a{
  text-decoration: none;
}

.menu-item:hover{
  color: #9ca0a4;
}

.add-tweet{
position: relative;
background-color: #15202B;
color:#CCCCCC;
border: 1px solid #CCCCCC;
height: 160px;

  width:400px;
border-radius: 5px;
}

.home{
display: flex;
flex-direction: column;
align-items: center;
margin-top: 40px;
height:100%;

padding:50px 170px;
} 

.tweet{
  color: #FFFFFF;
  background-color: #343A40;
  width: 400px;
  border-radius:5px;
  margin: 5px;
  overflow-wrap: break-word;
}


.name{
  color:#6C757D;
  padding: 5px;
}

.tweet-date{
  color:#6C757D;
  padding: 5px;
  font-size:13px;
}

.tweet-text{
  color:#ffffff;
  padding: 10px;
  font-size:12px;
  width:70%;
}

.add-form{
  position: relative;
}

.tweet-button{
  color:white;
  position: absolute;
  width: 68px;
  height: 34px;
  right:10px;
  bottom: 10px;
  background: #007BFF;
  border-radius: 4px;
  cursor: pointer;
}

.tweets{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.profile{
  position: absolute; 
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  margin-top: 40px;
  height:100%;
  max-width: 600px;
  padding:50px 20%;
  } 


  .profile-title{
    color:white;
    font-size:30px;
  }

.profile-form{
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 40px ;
  padding-top: 10px ;
}

.input-user-name{
  background-color: #15202B;
  border: 1px solid white;
  color:white;
  border-radius: 4px;
  max-width: 600px;
  height: 40px;
  margin-top:5px;
}

.btn{
  background-color:
  #007BFF;
  width: 68px;
  height: 34px;
  border-radius: 5px;
  position: absolute;
  bottom: 0;
  right:0;
  color:white;
 cursor: pointer;
}

.warning{
  position: absolute;
  bottom:6px;
  left: 2px;
  font-size: 12px;
  color: #721C24;
  background-color:  #F8D7DA;
  border-radius: 4px;
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  margin: 60px auto;
  font-size: 10px;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.display-eror{
  color: white;
}
